import React from 'react'
import { MainLayout } from './MainLayout'
import cardPreviewImgs from '../assets/img/cardPreviewImg.png'
import personImg1 from '../assets/img/personImg1.png'
import personImg2 from '../assets/img/personImg2.png'
import personImg3 from '../assets/img/personImg3.png'
export function ThreadSection() {
    return (
        <section className="thread">
            <MainLayout>
                <div className="title">
                    <span id="content1">Delivering real results for top</span><span id="content2">companies. Some of our <span className='color'>success stories.</span></span>
                </div>
                <div className="card-person-container flex">
                    <div className="card-preview">
                        <div className="person-desc">
                            <img src={cardPreviewImgs} />
                            <div className="txt"> “The team perfectly fit the specialized skill set required. They focused on the most essential features helping us launch the platform eight months faster than planned.”</div>
                        </div>
                        <div className="name">Kady Baker</div>
                        <div className="work-desc">Product Manager at Bookmark</div>
                        <img img src={personImg1} />
                    </div>
                    <div className="card-preview">
                        <div className="person-desc">
                            <img src={cardPreviewImgs} />
                            <div className="txt">“We needed to automate our entire onboarding process. The team came in and built out the whole journey. Since going live, user retention has gone through the roof!”</div>
                        </div>
                        <div className="name">Aiysha Reese</div>
                        <div className="work-desc">Founder of Manage</div>
                        <img img src={personImg2} />
                    </div>
                    <div className="card-preview">
                        <div className="person-desc">
                            <img src={cardPreviewImgs} />
                            <div className="txt"> “Amazing. Our team helped us build an app that delivered a new experience for hiring a physio. The launch was an instant success with 100k downloads in the first month.”</div>
                        </div>
                        <div className="name">Arthur Clarke</div>
                        <div className="work-desc">Co-founder of MyPhysio</div>
                        <img img src={personImg3} />
                    </div>

                </div>
            </MainLayout>
        </section>
    )
}
